@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap');

* { 
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
}

.app {
    height: 100vh;
    max-width: 100%;
    width: 100vw;
    overflow-x: hidden;
    background-color: black;
    padding: 2rem;
    
    > * {
      margin-bottom: 1.5rem;
    }
  }
  